 .box {
     width: 300px;
     padding: 40px;
     position: fixed;
     top: 50%;
     left: 50%;
     transform: translate(-50%, -50%);
     background: #191919;
     text-align: center;
     z-index: 999;
     display: none;
 }

 .box h1 {
     color: #ffffff;
     text-transform: uppercase;
     font-weight: 500;
 }

 .box input[type="text"],
 .box input[type="email"] {
     border: 0;
     background: none;
     display: block;
     margin: 20px auto;
     text-align: center;
     border: 2px solid #3498db;
     padding: 14px 10px;
     width: 200px;
     outline: none;
     color: white;
     border-radius: 24px;
     transition: 0.25s;
 }

