 .box input[type="submit"] {
     border: 0;
     background: none;
     display: block;
     margin: 20px auto;
     text-align: center;
     border: 2px solid #2ecc71;
     padding: 14px 40px;
     width: 200px;
     outline: none;
     color: white;
     border-radius: 24px;
     transition: 0.25s;
     cursor: pointer;
 }

 .box input[type="submit"]:hover {
     background: #2ecc71;
 }

 .close {
     color: #aaa;
     font-size: 50px;
     font-weight: bold;
     cursor: pointer;
     position: absolute;
     top: -10px;
     right: 10px;
 }