.products {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.products__inner {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.products__item {
    position: relative;
    background-color: $white;
    margin-bottom: 35px;
}

.products__img {
    vertical-align: top;

}

.products__item:hover::after {
    content: "";
    position: absolute;
    width: 57px;
    height: 54px;
    display: block;
    background-color: $yellow;
    top: 0;
    left: 0;
    cursor: pointer;
}

.products__item:hover::before {
    content: "";
    position: absolute;
    width: 36px;
    height: 36px;
    background: url("../../images/card-svg.png") no-repeat;
    left: 10px;
    top: 9px;
    background-size: cover;
    display: block;
    z-index: 1;
    cursor: pointer;
}

.products__title {
    @include font-text(20px, 23px, 600);
    text-align: center;
    color: $black;
    margin-top: 60px;
    margin-bottom: 44px;
    letter-spacing: 0.12em;
    text-transform: uppercase;
}

.products__item-title {
    @include font-text(16px, 21px, 600);
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;
    padding: 35px 0
}


@media (min-width: 574px) {
    .products {
        flex-direction: row;
    }

    .products__item {
        width: 48%;
    }

}


@media (min-width: $desktop-width) {
    .products__title {
        @include font-text(26px, 23px, 700);
        margin-top: 222px;
        margin-bottom: 75px;
        margin-left: 70px;
        text-transform: none;
    }

    .products__item {
        max-width: 370px;
        margin-bottom: 44px;
    }


    .products__item:hover {
        transform: translate(0, -50px);
    }

    .products__item-title {
        @include font-text(18px, 21px, 600);
        padding: 0;
        //  padding: 49px 0 33px 0;
        text-align: left;
        font-weight: bold;
        padding-left: 30px;
        max-width: 270px;
        height: 115px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .products__item--center {
        margin-top: -20px;
    }

    .products__item--center .products__item-title {
        padding: 34px 30px;
    }
}