/*Центрирование по середине экрана*/
.dws {
  position: fixed;
  top: 90%;
  right: 0;
  transform: translate(-90%, -90%);
  z-index: 999; }

.pulse,
.block,
.phone {
  position: absolute;
  width: 90px;
  height: 90px;
  color: #fff;
  text-align: center;
  border-radius: 50%; }

.block {
  cursor: pointer;
  z-index: 1;
  overflow: hidden; }

.pulse {
  text-align: center;
  background: #5cc2e2;
  position: relative;
  transition: 5s; }

.phone {
  font-size: 50px;
  line-height: 94px;
  animation: phone .9s ease-in-out infinite; }

.text {
  opacity: 0;
  position: absolute;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 15px;
  top: 28px;
  transform: scaleX(-1);
  transition: .5s ease-in-out; }

.phone i {
  transition: .5s ease-in-out; }

.pulse:hover {
  background: #29aee3; }

.pulse:hover i {
  transition: .5s ease-in-out;
  transform: scale(0); }

.pulse:hover .text {
  transform: scaleX(1);
  transition: .5s ease-in-out;
  opacity: 1; }

.pulse:hover::before,
.pulse:hover::after {
  content: "";
  position: absolute;
  border: 1px solid #29aee3;
  left: -20px;
  right: -20px;
  top: -20px;
  bottom: -20px;
  border-radius: 50%;
  animation: pulse 1.5s linear infinite; }

.pulse:hover::after {
  animation-delay: .5s; }

@keyframes phone {
  0%,
  100% {
    transform: rotate(-20deg); }
  50% {
    transform: rotate(21deg); } }

@keyframes pulse {
  0% {
    transform: scale(0.5);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    transform: scale(1.2);
    opacity: 0; } }

.box {
  width: 300px;
  padding: 40px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #191919;
  text-align: center;
  z-index: 999;
  display: none; }

.box h1 {
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 500; }

.box input[type="text"],
.box input[type="email"] {
  border: 0;
  background: none;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 2px solid #3498db;
  padding: 14px 10px;
  width: 200px;
  outline: none;
  color: white;
  border-radius: 24px;
  transition: 0.25s; }

.box input[type="submit"] {
  border: 0;
  background: none;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 2px solid #2ecc71;
  padding: 14px 40px;
  width: 200px;
  outline: none;
  color: white;
  border-radius: 24px;
  transition: 0.25s;
  cursor: pointer; }

.box input[type="submit"]:hover {
  background: #2ecc71; }

.close {
  color: #aaa;
  font-size: 50px;
  font-weight: bold;
  cursor: pointer;
  position: absolute;
  top: -10px;
  right: 10px; }
